<template>
  <div class="index">
    <!-- 头部 -->
    <Head></Head>
    <!-- main -->
    <Main></Main>
    <!-- footer -->
    <Footer></Footer>
  </div>
</template>

<script>
import Head from "../home/head";
import Main from "../statistics/main";
import Footer from "../home/footer";
export default {
  components: {
    Head,
    Main,
    Footer
  },
  data() {
    return {};
  }
};
</script>

<style scoped>
.index {
  display: flex;
  flex-direction: column;
  height: 100%;
}
</style>


