<template>
  <div>
    <mt-swipe :auto="4000" class="swiper" :show-indicators="false">
      <mt-swipe-item v-for="(item,index) in pic" :key="index">
        <img :src="item" alt />
      </mt-swipe-item>
    </mt-swipe>
  </div>
</template>

<script>
import { Swipe, SwipeItem } from "mint-ui";
export default {
  data() {
    return {
      pic: ["../../../static/img/30.png", "../../../static/img/30.png"]
    };
  }
};
</script>

<style scoped>
.swiper {
  width: 100%;
  height: 10rem;
}
.swiper img {
  width: 100%;
  height: 10rem;
}
</style>


