<template>
  <div style="margin-bottom:4.3rem;">
    <router-link
      class="list"
      v-for="(item,index) in list"
      :key="index"
      :to="item.path"
      :class="{active:currentIndex==index}"
      @click="changeSelect(index)"
    >
      <div class="line ju-ar al-it fl-ex">
        <div class="picture ju-ar al-it fl-ex">
          <img :src="item.pic1" alt="图片" />
          <p>{{item.p}}</p>
        </div>
        <div class="img al-it fl-ex">
          <img :src="item.pic2" alt="图片" />
        </div>
      </div>
    </router-link>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      currentIndex: 0,
      list: [
        {
          pic1: "../../../static/img/37.png",
          p: "会员统计",
          pic2: "../../../static/img/26.png",
          path: "/Member-statistics"
        },
        {
          pic1: "../../../static/img/31.png",
          p: "困难帮扶统计",
          pic2: "../../../static/img/26.png",
          path: "/Difficult-statistics"
        },
        {
          pic1: "../../../static/img/32.png",
          p: "劳模管理统计",
          pic2: "../../../static/img/26.png",
          path: "/Model-statistics"
        },
        {
          pic1: "../../../static/img/33.png",
          p: "志愿服务统计",
          pic2: "../../../static/img/26.png",
          path: "/Volunteer-statistics"
        },
        {
          pic1: "../../../static/img/34.png",
          p: "社会活动统计",
          pic2: "../../../static/img/26.png",
          path: "/Society-statistics"
        },
        {
          pic1: "../../../static/img/35.png",
          p: "普惠活动统计",
          pic2: "../../../static/img/26.png",
          path: "/Hp-statistics"
        },
        {
          pic1: "../../../static/img/36.png",
          p: "劳动就业统计",
          pic2: "../../../static/img/26.png",
          path: "/Work-statistics"
        }
      ]
    };
  },
  methods: {
    changeSelect(index) {
      console.log(this.$router.push);
      //点击切换成选中的样式，把变量的值赋值为 index
      this.currentIndex = index;
      var _this = this;
      _this.$router.push({
        path: _this.itemList[index].path
      });
    }
  }
};
</script>

<style scoped>
.list {
  width: 100%;
  height: 5.6rem;
}
.list .line {
  width: 90%;
  height: 4.5rem;
  margin: 0.55rem 5% 0.55rem 5%;
  border: 0.05rem solid #ededed;
}
.list .line .img {
  width: 30%;
  height: 4.5rem;
}
.list .line .img img {
  width: 17%;
  height: 1.25rem;
  margin-left: 75%;
}
.list .line .picture {
  width: 56%;
  height: 4.5rem;
}
.list .line .picture img {
  width: 30%;
  height: 3.4rem;
}
.list .line .picture p {
  font-size: 0.9rem;
  color: black;
  font-weight: bold;
}
</style>


